export default {
	
	data() {
		return {
			id: 0,
			level: '',
			name: '',
			ratio: ''
		};
	},

	onLoad(options) {
		this.id = options.id
		this.init()
	},
	methods: {
		init() {
			//产品详情
			uni.request({
				url: this.baseUrl + "/product-detail",
				data: {
					Id: this.id
				},
				header: {
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					if(this.$base._indexOf(res.data.status)){
						this.$base._isLogin()
					}else if (res.data.status == 1) {
						this.level = res.data.data.NeedLevel
						this.name = res.data.data.Name
						this.ratio = res.data.data.Ratio

					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				}
			})
		},
		//立即预约
		order(){
			//跳转到购买页面
			uni.navigateTo({
				url:"./buy?id="+this.id
			})
		},
		forgetPassword(){
			//忘记密码
			uni.navigateTo({
				url:"../login/login"
			})
		},
		showLevelBgc(level){
			if(level==1){
				return  'linear-gradient(#FF727C, #FFA8AE)'
			}else if(level==2){
				return  'linear-gradient(#7FCCFF, #0099FF)'
			}else if(level==3){
				return  'linear-gradient(#FFC744, #FF9100)'
			}
		},
	}
}
